<template>
    <div>
        <div class="body body1"></div>
        <div class="body body2"></div>
        <div class="body body3"></div>
        <div class="body body4"></div>
        <div class="body body5"></div>
        <el-dialog title="" :visible.sync="dialogVisible" width="30%">
            <p>(1)101,50</p>
            <p>(2)5</p>
            <p>(3)7</p>
            <p>(4)boss,</p>
            <p>(5)</p>
            <p>(6)</p>
            <p>(7)?,</p>
        </el-dialog>

        <el-dialog title="" :visible.sync="mbtiDialogVisible" width="70%" center :before-close="handleClose">
            <div class="dialog-content">
                <div v-for="(item, index) in mbti" :key="index">
                    <div @click="joinMbti(item)">
                        <el-card class="box-card2">
                            <div slot="header" style="text-align: center;">
                                <span>{{ item[0] }}</span>
                            </div>
                            <div style="text-align: center;">{{ item[1] }}</div>
                            <el-image style="width: 50px; height: 80px;margin-left: 30px;"
                                :src="require('../../assets/' + item[0] + '.jpeg')">
                                <div slot="placeholder" class="image-slot">
                                    <span class="dot">...</span>
                                </div>
                            </el-image>
                        </el-card>
                    </div>
                </div>
            </div>
            <el-button style="position: relative;left: 45%;margin-top: 20px;">x{{ refresh }}</el-button>
        </el-dialog>

        <el-dialog title="" :visible.sync="centerDialogVisible" width="70%" center :before-close="handleClose">
            <div class="dialog-content">
                <div v-for="(item, index) in power" :key="index">
                    <div @click="joinPower(item)">
                        <el-card>
                            <div slot="header" style="text-align: center;">
                                <span>{{ item[0] }}</span>
                            </div>
                            <div style="text-align: center;">{{ item[1] }}</div>
                            <el-image style="width: 300px; height: 500px;"
                                :src="require('../../assets/' + item[0] + '.jpeg')">
                                <div slot="placeholder" class="image-slot">
                                    <span class="dot">...</span>
                                </div>
                            </el-image>
                        </el-card>
                    </div>
                </div>
            </div>
            <el-button style="position: relative;left: 45%;margin-top: 20px;" @click="refreshPower()"
                :disabled="this.refresh == 0">x{{ refresh }}</el-button>
        </el-dialog>

        <el-dialog title="" :visible.sync="thingDialogVisible" width="70%" center>
            <div style="text-align: center;">
                <h1>{{ thing[thing.length - 1][0] }}</h1>
                <h2>{{ thing[thing.length - 1][1] }}</h2>
                <el-image style="width: 300px; height: 500px"
                    :src="require('../../assets/' + thing[thing.length - 1][0] + '.jpeg')">
                    <div slot="placeholder" class="image-slot">
                        <span class="dot">...</span>
                    </div>
                </el-image>
            </div>
        </el-dialog>

        <el-container>
            <el-header class="header">
                <el-switch v-model="value1" active-text="" inactive-text="" class="switch"></el-switch>
                <h1 style="color: white;"> {{ chapter }} {{ round }}</h1>
                <h2 style="color: white;">:{{ area }}</h2>
                <audio controls class="music">
                    <source src="../../assets/Die For You.flac">
                     audio 
                </audio>
                <el-image :src="require('../../assets/3.gif')" :style="style1" style="margin-top: 3%;z-index: 1;">
                    <div slot="placeholder" class="image-slot">
                        <span class="dot">...</span>
                    </div>
                </el-image>
            </el-header>
            <el-container>
                <el-aside>
                    <el-popover placement="left" v-model="visible" trigger="hover">
                        <h3></h3>
                        <div v-for="(item, index) in thing" :key="index">
                            <p>{{ index + 1 }}.{{ item[0] }}:{{ item[1] }}</p>
                            <el-image style="width: 100px; height: 150px"
                                :src="require('../../assets/' + item[0] + '.jpeg')">
                                <div slot="placeholder" class="image-slot">
                                    <span class="dot">...</span>
                                </div>
                            </el-image>
                        </div>
                        <el-button slot="reference" class="thing"></el-button>
                    </el-popover>
                    <div v-for="(item, index) in mePower" :key="index">
                        <el-tooltip :content="item[1]" placement="right-start" effect="light" class="tooltip">
                            <el-button>{{ item[0] }}
                                <el-image style="width: 50px; height: 70px;display: block;margin: auto;"
                                    :src="require('../../assets/' + item[0] + '.jpeg')">
                                    <div slot="placeholder" class="image-slot">
                                        <span class="dot">...</span>
                                    </div>
                                </el-image>
                            </el-button>
                        </el-tooltip>
                    </div>
                </el-aside>
                <el-main>
                    <section class="center-panel">
                        <div class="stage" v-loading="loading">
                            <el-card class="box-card1">
                                <el-progress :text-inside="true" :stroke-width="26"
                                    :percentage="me[0].size > 100 || me[0].size < 0 ? 100 : me[0].size"></el-progress>
                                <el-tag v-for="item in me" :key="item.label" :type="item.type" class="tags">
                                    {{ item.label }}:{{ item.size }}
                                </el-tag>
                            </el-card>
                            <div>
                                <div>
                                    <el-card style="width: 700px;height: 130px;overflow: auto;">
                                        <div v-for="(item, index) in items1" :key="index">
                                            <span v-if="item[1] == 99" style="color: lightcoral;">[boss]</span>
                                            <span v-else style="color: lightseagreen;">[]</span>
                                            {{ item[0] }}:{{ item[3] }}
                                        </div>
                                    </el-card>
                                </div>
                                <draggable v-model="items1" group="people" animation="0" @start="onStart" @end="onEnd">
                                    <transition-group class="buttons" :style="style">
                                    </transition-group>
                                </draggable>
                            </div>
                            <el-card class="box-card1">
                                <el-progress :text-inside="true" :stroke-width="26"
                                    :percentage="boss[0].size > 100 || boss[0].size < 0 ? 100 : boss[0].size"
                                    status="exception"></el-progress>
                                <el-tag v-for="item in boss" :key="item.label" :type="item.type" class="tags">
                                    {{ item.label }}:{{ item.size }}
                                </el-tag>
                            </el-card>
                        </div>
                        <div class="buttons">
                            <div class="left_buttons">
                                <el-button class="el_button">:{{ energy }}</el-button>
                                <el-button class="el_button" @click="endTurn()"></el-button>
                            </div>
                            <draggable v-model="items" group="people" animation="0" @start="onStart" @end="onEnd($event)"
                                :sort="false">
                                <transition-group class="buttons" :style="style">
                                    <div v-for="(item, index) in items" :key="index">
                                        <el-popover placement="top">
                                            <div>
                                                {{ item[1] }}
                                            </div>
                                            <div>
                                                {{ item[2] }}
                                            </div>
                                            <div>
                                                {{ item[3] }}
                                            </div>
                                            <el-card class="box-card" slot="reference">
                                                <div>
                                                    {{ item[0] }}
                                                </div>
                                            </el-card>
                                        </el-popover>
                                    </div>
                                </transition-group>
                            </draggable>
                        </div>
                        <div class="buttons">
                            <div class="left_buttons">
                                <el-button class="el_button">{{ money }}</el-button>
                                <el-button class="el_button" @click="getRandom()">(2)</el-button>
                            </div>
                            <div class="buttons" v-for="(item, index) in shop" :key="index">
                                <div @click="goumai(item, index)">
                                    <el-card class="box-card1">
                                        <div>
                                            {{ item[0] }}
                                        </div>
                                        <div>
                                            {{ item[1] }}
                                        </div>
                                        <div>
                                            {{ item[2] }}
                                        </div>
                                        <div>
                                            {{ item[2] * 2 }}
                                        </div>
                                        <div>
                                            {{ item[3] }}
                                        </div>
                                    </el-card>
                                </div>
                            </div>
                        </div>
                    </section>
                </el-main>
                <el-aside>
                    <el-button class="rule" @click="dialogVisible = true"></el-button>
                    <div v-for="(item, index) in bossPower" :key="index">
                        <el-tooltip :content="item[1]" placement="right-start" effect="light" class="tooltip">
                            <el-button>{{ item[0] }}
                                <el-image style="width: 50px; height: 70px;display: block;margin: auto;"
                                    :src="require('../../assets/' + item[0] + '.jpeg')">
                                    <div slot="placeholder" class="image-slot">
                                        <span class="dot">...</span>
                                    </div>
                                </el-image>
                            </el-button>
                        </el-tooltip>
                    </div>
                </el-aside>
            </el-container>
        </el-container>
    </div>
</template>

<script>
import { shop1, power1, thing1, mbti1, xingzuo1 } from '@/utils/index.js';
import draggable from 'vuedraggable'
export default {
    name: 'Vip',
    data() {
        return {
            visible: false,
            dialogVisible: false,
            centerDialogVisible: false,
            thingDialogVisible: false,
            mbtiDialogVisible: false,
            loading: false,
            items: [["", 10, 1, "+10"], ["", 0, 1, "+10"], ["", 0, 1, "+10"]],
            items1: [],
            items2: 0,
            shop: [],
            power: [],
            thing: [],
            mePower: [],
            bossPower: [xingzuo1[0]],
            mbti: mbti1,
            xingzuo: xingzuo1,
            me: [
                { type: 'info', label: '', size: 100 },
                { type: 'info', label: '', size: 10 },
                { type: 'info', label: '', size: 0 },
                { type: 'info', label: '', size: "" },
            ],
            boss: [
                { type: 'info', label: '', size: 100 },
                { type: 'info', label: '', size: 20 },
                { type: 'info', label: '', size: 10 },
                { type: 'info', label: 'boss', size: xingzuo1[0][0] },
            ],
            energy: 5,
            round: 1,
            chapter: 1,
            money: 32,
            refresh: 1,
            round1: 0,
            round2: 0,
            area: "",
            drag: false,
            style: "min-height:120px;",
            style1: "display:none;",
            value1: false,
            isDied: false,
        }
    },
    components: {
        draggable,
    },
    methods: {
        //
        onStart() {
            this.drag = true;
            this.items2 = this.items1.length;
        },
        //
        onEnd(e) {
            this.drag = false;
            if (this.items1.length > this.items2) {
                this.useMe(e.item._underlying_vm_, 99);
                this.items1.shift();
            }
        },
        //
        useMe(item, index) {
            if (item[2] > this.energy) {
                this.items.unshift(this.items1[0])
                return this.$message.error('');
            }
            if (item[0] == "") {
                this.me[0].size += 10;
                this.quchu(item, index);
            } else if (item[0] == "") {
                this.me[1].size += item[1];
                this.quchu(item, index);
            } else if (item[0] == "") {
                this.me[2].size += 10;
                this.quchu(item, index);
            } else if (item[0] == "") {
                this.me[1].size += item[1];
                this.quchu(item, index);
            } else if (item[0] == "") {
                this.me[1].size *= 2;
                this.quchu(item, index);
            } else if (item[0] == "") {
                this.me[2].size *= 2;
                this.quchu(item, index);
            } else if (item[0] == "") {
                this.boss[1].size = 0;
                this.quchu(item, index);
            } else if (item[0] == "") {
                this.me[1].size += this.me[2].size;
                this.quchu(item, index);
            } else if (item[0] == "") {
                this.me[1].size += item[1];
                this.me[0].size -= 5;
                this.quchu(item, index);
            } else if (item[0] == "") {
                this.getRandom();
                this.money += 2;
                this.quchu(item, index);
            } else if (item[0] == "") {
                this.energy += 1;
                this.quchu(item, index);
            }
        },
        //
        quchu(item, index) {
            for (let i = 0; i < this.items.length; i++) {
                if (i == index) {
                    this.items.splice(i, 1);
                }
            }
            this.energy -= item[2];
            this.items1.unshift(item);
        },
        //
        goumai(item, index) {
            if (this.items.length == 9) return this.$message.error('');
            if (this.money >= item[2] * 2) {
                this.money -= item[2] * 2;
                this.items.push(item);
                for (let i = 0; i < this.shop.length; i++) {
                    if (i == index) {
                        this.shop.splice(i, 1);
                    }
                }
            } else {
                this.$message.error('');
            }
        },
        //
        startTurn() {
            for (let i = 0; i < this.thing.length; i++) {
                if (this.thing[i][0] == "") {
                    this.me[1].size += 20;
                } else if (this.thing[i][0] == "") {
                    this.energy += 1;
                } else if (this.thing[i][0] == "") {
                    this.energy -= 1;
                } else if (this.thing[i][0] == "") {
                    this.money += 5;
                }
            };
            if (this.boss[3].size == "") {
                this.energy -= 1;
            } else if (this.boss[3].size == "") {
                this.money -= 3;
            } else if (this.boss[3].size == "") {
                this.me[2].size -= 10;
            } else if (this.boss[3].size == "") {
                this.boss[1].size += 10;
            }
        },
        //
        endTurn() {
            // this.loading = true;
            this.style1 = "display:inline-block";
            setTimeout(() => {
                // this.loading = false;
                this.style1 = "display:none";
            }, 1000);
            for (let i = 0; i < this.mePower.length; i++) {
                if (this.mePower[i][0] == "") {
                    var arr = this.items.some(function (x) {
                        return x[1] !== 0;
                    });
                    if (arr == false) this.me[1].size *= 2;
                } else if (this.mePower[i][0] == "") {
                    var arr2 = this.items.filter(function (x) {
                        return x[1] !== 0;
                    });
                    arr2.forEach(function (x) {
                        x[1] * 2 <= 40 ? x[1] *= 2 : x[1] = 40;
                    });
                }
            }
            this.items1.unshift(["", 98, 98, "" + (this.me[1].size - this.boss[2].size) + ""])
            this.items1.unshift(["", 99, 99, "" + (this.boss[1].size - this.me[2].size) + ""])
            this.boss[0].size += this.boss[2].size - this.me[1].size;
            this.me[0].size += this.me[2].size - this.boss[1].size;
            this.me[0].size > 100 ? this.me[0].size = 100 : this.me[0].size;
            this.me[1].size = 10;
            this.me[2].size = 0;
            this.boss[1].size = 20;
            this.boss[2].size = 10;
            this.round++;
            this.energy = 5;
            //10150
            if (this.money <= 50) {
                this.money += Math.floor(this.money / 10);
            } else {
                this.money += 5;
            }
            this.money += this.round;
            this.active++;
        },
        //()
        getRandom() {
            if (this.money < 2) return this.$message.error('');
            this.money -= 2;
            this.shop = [];
            //5
            for (let i = 0; i < 4; i++) {
                let num = Math.floor(Math.random() * shop1.length);
                if (this.shop.indexOf(shop1[num]) == -1) {
                    this.shop.push(shop1[num]);
                } else {
                    i--;
                }
            }
        },
        //()
        getRandomNO() {
            this.shop = [];
            //5
            for (let i = 0; i < 4; i++) {
                let num = Math.floor(Math.random() * shop1.length);
                if (this.shop.indexOf(shop1[num]) == -1) {
                    this.shop.push(shop1[num]);
                } else {
                    i--;
                }
            }
        },
        //
        getRandomPower() {
            this.power = [];
            for (let i = 0; i < 3; i++) {
                let num = Math.floor(Math.random() * power1.length);
                if (this.power.indexOf(power1[num]) == -1 && this.mePower.indexOf(power1[num]) == -1) {
                    this.power.push(power1[num]);
                } else {
                    i--;
                }
            }
        },
        //
        getRandomThing() {
            let num = Math.floor(Math.random() * thing1.length);
            if (this.thing.indexOf(thing1[num]) == -1) {
                this.thing.push(thing1[num]);
            } else {
                this.getRandomThing();
            }
            if (thing1[num][0] == "") {
                this.me[0].size = 100; this.boss[0].size += 100;
            } else if (thing1[num][0] == "") {
                this.me[0].size -= 30; this.boss[0].size -= 30;
                this.isWin();
            } else if (thing1[num][0] == "") {
                this.me[0].size = Math.round(Math.random() * 99);
                this.boss[0].size = Math.round(Math.random() * 99);
                this.me[1].size = Math.round(Math.random() * 99);
                this.boss[1].size = Math.round(Math.random() * 99);
                this.me[2].size = Math.round(Math.random() * 99);
                this.boss[2].size = Math.round(Math.random() * 99);
            } else if (thing1[num][0] == "") {
                this.items = [["", 10, 1, ""], ["", 10, 1, ""], ["", 10, 1, ""]],
                    this.shop = [],
                    this.power = [],
                    this.thing = [],
                    this.mePower = [],
                    this.bossPower = [],
                    this.me = [
                        { type: 'info', label: '', size: 100 },
                        { type: 'info', label: '', size: 10 },
                        { type: 'info', label: '', size: 0 },
                    ],
                    this.boss = [
                        { type: 'info', label: '', size: 100 },
                        { type: 'info', label: '', size: 20 },
                        { type: 'info', label: '', size: 0 },
                    ],
                    this.energy = 3,
                    this.round = 1,
                    this.chapter = 1,
                    this.money = 32,
                    this.refresh = 1
            } else if (thing1[num][0] == "") {
                this.me[0].size = Math.round(Math.random() * 99);
                this.me[1].size = Math.round(Math.random() * 99);
                this.me[2].size = Math.round(Math.random() * 99);
            } else if (thing1[num][0] == "") {
                this.me[1].size += 30;
            } else if (thing1[num][0] == "") {
                this.me[2].size += 30;
            } else if (thing1[num][0] == "") {
                this.me[0].size += 50;
            } else if (thing1[num][0] == "") {
                this.energy += 3;
            } else if (thing1[num][0] == "") {
                this.money += 30;
            } else if (thing1[num][0] == "") {
                this.me[0].size *= 2;
                this.me[1].size *= 2;
                this.me[2].size *= 2;
            } else if (thing1[num][0] == "") {
                this.me[1].size *= 3;
            } else if (thing1[num][0] == "") {
                this.boss[1].size = 0;
            } else if (thing1[num][0] == "") {
                this.boss[0].size *= 2;
                this.boss[1].size *= 2;
                this.boss[2].size *= 2;
            } else if (thing1[num][0] == "") {
                this.boss[0].size = Math.round(Math.random() * 99);
                this.boss[1].size = Math.round(Math.random() * 99);
                this.boss[2].size = Math.round(Math.random() * 99);
            } else if (thing1[num][0] == "") {
                if (this.me[0].size >= this.boss[0].size) {
                    this.$message.success('');
                    this.round = 1;
                    this.me[0].size = 100;
                    this.chapter++;
                    this.boss[0].size += this.chapter * 100;
                } else {
                    this.$message.error('');
                }
            } else if (thing1[num][0] == "") {
                this.refresh++;
            }
        },
        //
        refreshPower() {
            this.getRandomPower();
            this.refresh -= 1;
        },
        handleClose() {
            return this.$message.info('');
        },
        //
        joinPower(power) {
            this.mePower.push(power);
            this.centerDialogVisible = false;
            if (power[0] == "") {
                this.me[0].size += 50;
            } else if (power[0] == "") {
                if (this.money <= 20) this.money += 30;
            } else if (power[0] == "") {
                this.money += 20;
            }
        },
        //
        joinMbti(item) {
            this.mbtiDialogVisible = false;
            this.me[3].size = item[0];
            this.mePower.push(item);
            if (this.me[3].size == "ISFP") {
                this.money += 20;
            } else if (this.me[3].size == "INFP") {
                this.refresh++;
            }
        },
        //
        isPower() {
            for (let i = 0; i < this.mePower.length; i++) {
                if (this.mePower[i][0] == "") {
                    this.me[0].size += 5;
                } else if (this.mePower[i][0] == "" && this.isDied == false) {
                    if (this.me[0].size <= 0) this.me[0].size = 30;
                    this.isDied = true;
                } else if (this.mePower[i][0] == "" && this.isDied == false) {
                    if (this.me[0].size <= 0) this.me[0].size = 1;
                    this.isDied = true;
                } else if (this.mePower[i][0] == "") {
                    this.money += 5;
                } else if (this.mePower[i][0] == "") {
                    this.round1++;
                    if (this.round1 == 3) this.money += 30;
                } else if (this.mePower[i][0] == "") {
                    this.me[1].size += 10;
                } else if (this.mePower[i][0] == "") {
                    this.energy += 2;
                } else if (this.mePower[i][0] == "") {
                    this.me[2].size += 10;
                } else if (this.mePower[i][0] == "") {
                    this.money += 2;
                } else if (this.mePower[i][0] == "") {
                    this.round2++;
                    if (this.round2 == 5)
                        this.money += 5;
                    this.me[1].size += 20;
                    this.me[2].size += 10;
                }
            }
            if (this.me[3].size == "ISTJ") {
                this.energy++;
            } else if (this.me[3].size == "ISFJ") {
                this.me[1].size += 10;
            } else if (this.me[3].size == "INFJ") {
                this.me[2].size += 10;
            } else if (this.me[3].size == "INTJ") {
                this.me[0].size += 3;
            } else if (this.me[3].size == "ISTP") {
                this.money += 3;
            } else if (this.me[3].size == "INTP") {
                this.me[0].size += 5;
                this.me[1].size += 5;
                this.me[2].size += 5;
            }
        },
        isWin() {
            if (this.me[0].size <= 0) {
                this.$message.error('');
            }
            if (this.boss[0].size <= 0) {
                this.$message.success('');
                this.round = 1;
                this.me[0].size = 100;
                this.chapter++;
                this.boss[0].size += this.chapter * 100;
            }
        },
        bodyScale() {
            let devicewidth = document.documentElement.clientWidth//
            let scale = devicewidth / 1920 // 
            document.body.style.zoom = scale//
        }
    },
    mounted() {
        window.onresize = () => {
            return (() => {
                // 
                this.bodyScale()
            })()
        }
    },
    created() {
        this.mbtiDialogVisible = true;
        this.getRandom();
        this.thingDialogVisible = true;
        this.getRandomThing();
    },
    computed: {

    },
    watch: {
        round(newVal, oldVal) {
            this.startTurn();
            this.isPower();
            this.getRandomNO();
            this.isWin();
            if (newVal % 5 == 0) {
                this.centerDialogVisible = true;
                this.getRandomPower();
            }
            if (newVal % 7 == 0) {
                this.thingDialogVisible = true;
                this.getRandomThing();
            }
        },
        chapter(newVal, oldVal) {
            if (newVal == 2) {
                document.getElementsByClassName('body1')[0].style.display = 'none';
                this.area = "";
                this.boss[3].size = this.xingzuo[1][0];
                this.bossPower = [this.xingzuo[1]];
            } else if (newVal == 3) {
                document.getElementsByClassName('body2')[0].style.display = 'none';
                this.area = "";
                this.boss[3].size = this.xingzuo[2][0];
                this.bossPower = [this.xingzuo[2]];
            } else if (newVal == 4) {
                document.getElementsByClassName('body3')[0].style.display = 'none';
                this.area = "";
                this.boss[3].size = this.xingzuo[3][0];
                this.bossPower = [this.xingzuo[3]];
            } else if (newVal == 5) {
                document.getElementsByClassName('body4')[0].style.display = 'none';
                this.area = "";
                this.boss[3].size = this.xingzuo[3][0];
                this.bossPower = [this.xingzuo[3]];
            }
        },
        value1(newVal, oldVal) {
            let arr = document.getElementsByClassName('el-card');
            let arr1 = document.getElementsByClassName('el-button');
            if (newVal == true) {
                for (let i = 0; i < arr.length; i++) {
                    arr[i].style.backgroundColor = 'lightgray';
                }
                for (let i = 0; i < arr1.length; i++) {
                    arr1[i].style.backgroundColor = 'lightgray';
                }
            } else {
                for (let i = 0; i < arr.length; i++) {
                    arr[i].style.backgroundColor = '';
                }
                for (let i = 0; i < arr1.length; i++) {
                    arr1[i].style.backgroundColor = '';
                }
            }
        }
    },
}
</script>

